import { HttpClient, HttpHeaders } from '@angular/common/http';
import { stringify } from '@angular/compiler/src/util';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SharedService } from 'app/Services/Shared/shared.service';

const httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  }
@Component({
    selector: 'dashboard-cmp',
    moduleId: module.id,
    templateUrl: 'dashboard.component.html'
})

export class DashboardComponent implements OnInit{

    public api;
    public httpOptions:object = {};
    // 
    public transferAgentUrl;
    // 
    public allUsers;
    public getUsersLastMonth;
    public getUsersLastWeek;
    // 
    public AllOrders;
    public getOrdersLastMonth;
    public getOrdersLastWeek;
    // 
    public allUsersCout: any;
    public lastMonthUsersCount: any;
    public lastWeekUsersCount: any;
    // 
    public allOrdersCout: any;
    public lastMonthOrdersCount: any;
    public lastWeekOrdersCount: any;
    // 
    public averageSessionTime: any;
    public sessionsNumbers: any;
    public usersNumbers: any;
    // 
    constructor(protected http:HttpClient, private router:Router, private shared:SharedService )
    {
        this.api = this.shared.api;
        // 
        this.allUsers = `${this.api}/api/all-users`;
        this.getUsersLastMonth = `${this.api}/api/last-month-users`;
        this.getUsersLastWeek = `${this.api}/api/last-week-users`;
        // 
        this.AllOrders = `${this.api}/api/all-orders`;
        this.getOrdersLastMonth = `${this.api}/api/last-month-orders`;
        this.getOrdersLastWeek = `${this.api}/api/last-week-orders`;
        // 
        this.transferAgentUrl = `${this.api}/api/report/agent`;
    }

    ngOnInit(){
        this.http.get(this.allUsers,httpOptions).subscribe((res)=>
        {
            this.allUsersCout = res;
        },(error) => {});
        // 
        this.http.get(this.getUsersLastMonth,httpOptions).subscribe((res)=>
        {
            this.lastMonthUsersCount = res;
        },(error) => {});
        // 
        this.http.get(this.getUsersLastMonth,httpOptions).subscribe((res)=>
        {
            this.lastWeekUsersCount = res;
        },(error) => {});
        // *******************************Orders***********************************
        this.http.get(this.AllOrders,httpOptions).subscribe((res)=>
        {
            this.allOrdersCout = res;
        },(error) => {});
        // 
        this.http.get(this.getOrdersLastMonth,httpOptions).subscribe((res)=>
        {
            this.lastMonthOrdersCount = res;
        },(error) => {});
        // 
        this.http.get(this.getOrdersLastWeek,httpOptions).subscribe((res)=>
        {
            this.lastWeekOrdersCount = res;
        },(error) => {});
        // *******************************Transfer Agent***********************************
        this.http.get(this.transferAgentUrl,httpOptions).subscribe((res:any)=>
        {
            this.averageSessionTime = res.data.average;
            console.log(res.data.data);
        },(error) => {});
        // 
        this.http.get(this.transferAgentUrl,httpOptions).subscribe((res:any)=>
        {
            this.sessionsNumbers = res.data.sessions;
        },(error) => {});
        // 
        this.http.get(this.transferAgentUrl,httpOptions).subscribe((res:any)=>
        {
            this.usersNumbers = res.data.users;
        },(error) => {});
        // 
    }
    
}
