import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServiceInquiry, ServiceInquiryAdapter } from 'app/models/service_inquiry';
import { ServiceInquiryService } from 'app/Services/service_inquiry/service-inquiry.service';

@Component({
  selector: 'app-service-inquiry',
  templateUrl: './service-inquiry.component.html',
  styleUrls: ['./service-inquiry.component.css']
})
export class ServiceInquiryComponent implements OnInit {

  public data:ServiceInquiry[];
  public headers:Array<any>;
  public total:number;
  constructor(private inquiryService:ServiceInquiryService,private adapter:ServiceInquiryAdapter,private router:Router) { }

  ngOnInit(){

    this.inquiryService.getData(this.adapter).subscribe((data:ServiceInquiry[])=>
    {
      if (data) {
        hideloader();
    }
      this.total = this.inquiryService.total;
        this.data = data;
    })
    this.headers = this.inquiryService.getHeaders();
    // Function is defined
    function hideloader() {
  
      // Setting display of spinner
      // element to none
      document.getElementById('loadContainer')
          .style.display = 'none';
  }
}

  paginateData(event) : void
  {
      this.inquiryService.index = event;
      this.inquiryService.getData(this.adapter).subscribe((data:ServiceInquiry[])=>
      {
          this.data = data;
      })
  }
}
