import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ServiceGeneral } from 'app/interfaces/services';
import { BuisnessInquiry } from 'app/models/buisness_inquiry';
import { SharedService } from '../Shared/shared.service';

@Injectable({
  providedIn: 'root'
})
export class BuisnessInquiryService extends ServiceGeneral<BuisnessInquiry>{

  public api;
  public constructor(protected  http:HttpClient,private shared:SharedService)
  {
    super(http);
    this.api = shared.api;
    this.url = `${this.api}/api/buisness`;
    this.headers = ['name','email','mobile','type','related'];
  }
}
