import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SharedService } from 'app/Services/Shared/shared.service';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  })
}

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {
  public api;
  public httpOptions:object = {};
  //
  public usersData;
  public allUsersData: any;
  constructor(protected http:HttpClient, private router:Router, private shared:SharedService )
  {
    this.api = this.shared.api;
    // 
    this.usersData = `${this.api}/api/users-data`;
  }

  ngOnInit(): void {
    this.http.get(this.usersData,httpOptions).subscribe((res:any)=>
    {
      if (res) {
          hideloader();
      }
      this.allUsersData = res;
    },(error) => {});
    // Function is hideloader
    function hideloader() {
        document.getElementById('loadContainer')
            .style.display = 'none';
    }
  }

}
