import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AnswerAdapter } from 'app/models/answer';
import { AgentReportService } from 'app/Services/agent-report/agent-report.service';
import { AnswerReportService } from 'app/Services/answer-report/answer-report.service';
import { AnswerService } from 'app/Services/answer/answer.service';
import {jsPDF} from 'jspdf'

@Component({
  selector: 'app-agent-report',
  templateUrl: './agent-report.component.html',
  styleUrls: ['./agent-report.component.css']
})
export class AgentReportComponent implements OnInit {

  public data;
  public arrayData = [];
  public pages;
  public current = 0;
  public agents;
  public users = 0;
  public average = 0;
  public sessions = 0;
  public currentAgent;
  constructor(protected reportService:AgentReportService,protected router:Router) { }


  ngOnInit(): void {

    this.reportService.getData().subscribe((data:any) => 
    {
      data = data.data;
      this.users = data.users;
      this.average = data.average;
      this.sessions = data.sessions;
    });

    this.reportService.getAgents().subscribe((data:any) =>
    {
      if (data) {
        hideloader();
    }
      console.log(data.data); 
        this.agents = data.data;
    })
    // Function is defined
    function hideloader() {
  
      // Setting display of spinner
      // element to none
      document.getElementById('loadContainer')
          .style.display = 'none';
  }

  }


  getReport()
  {
    let agent = (<HTMLInputElement>document.getElementById('agents')).value;
    let start = (<HTMLInputElement>document.getElementById('from')).value;
    let end = (<HTMLInputElement>document.getElementById('to')).value;
    this.currentAgent = agent;
    // 
    this.data=[];
      document.getElementById('loadContainer')
          .style.display = 'block';
    // 
    this.reportService.getAgentData(agent,start,end).subscribe((data:any) =>
    {
      if (data) {
        hideloader();
    }
      this.data = data.data.agent_data;
      this.average = data.data.general.average;
      this.users = data.data.general.users;
      this.sessions = data.data.general.sessions;
    })
    // Function is defined
    function hideloader() {
  
      // Setting display of spinner
      // element to none
      document.getElementById('loadContainer')
          .style.display = 'none';
  }
  }

  getDetails(session)
  {
    this.reportService.getAgentUserLog(session).subscribe((data : any) => 
    {
      let pdf = new jsPDF('p', 'mm', [297, 210]);
      let text = pdf.splitTextToSize(data,180);
      pdf.text(text,15,20);
      pdf.save('report');
    })
  }

}
