import { Injectable } from "@angular/core";
import { AnswerService } from 'app/Services/answer/answer.service';
import { Adapter } from "../adapter";

export class Answer
{
    constructor(
        public id:number,
        public en:string,
        public text:string,
        public parentText:string,
        public parentId:number
    ) {}
}

@Injectable({
    providedIn: "root",
  })
  export class AnswerAdapter implements Adapter<Answer> {
    adapt(item: any): Answer {
      return new Answer(item.id, item.en, item.text, item.parent_text,item.parent_id);
    }
  }
