import { Component, OnInit } from '@angular/core';
import Chart from 'chart.js';
import {SuggestionService} from '../../Services/suggestion/suggestion.service';
import {Suggestion, SuggestionAdapter} from '../../models/suggestion';
import { KeysPipe } from 'app/pipes/keys.pipe';
import {map} from 'rxjs/operators';
import { AnswerService } from 'app/Services/answer/answer.service';
import { Answer, AnswerAdapter } from 'app/models/answer';
import { Router } from '@angular/router';



@Component({
    selector: 'suggestions',
    moduleId: module.id,
    templateUrl: 'suggestion.component.html',
    providers:[KeysPipe]
})

export class SuggestionComponent implements OnInit{
    public data:Suggestion[]
    public headers:Array<any>
    public answer:Answer;
    public total:number;

    public constructor(private suggestionService:SuggestionService,private  adapter:SuggestionAdapter,private  answerService:AnswerService, private adapter2:AnswerAdapter,private router:Router)
    {
    }

    ngOnInit(){
    
          this.suggestionService.getData(this.adapter).subscribe((data:Suggestion[])=>
          {
            if (data) {
                hideloader();
            }

            this.total = this.suggestionService.total;
              this.data = data;
          })
          this.headers = this.suggestionService.getHeaders();
          // Function is defined
        function hideloader() {
  
            // Setting display of spinner
            // element to none
            document.getElementById('loadContainer')
                .style.display = 'none';
        }
    }

    deleteData(data:Suggestion) : void
    {
        this.suggestionService.deleteData(data.id).subscribe( (data) =>
        {
            this.router.navigate(['/suggestions']);
        })
    }

    paginateData(event) : void
    {
        this.suggestionService.index = event;
        this.suggestionService.getData(this.adapter).subscribe((data:Suggestion[])=>
        {
            this.data = data;
        })
        this.headers = this.suggestionService.getHeaders();
    }

    getRecordData(event)
    {
        this.answerService.getItem(this.adapter2,event.answer).subscribe((data:Answer)=>
        {
            this.answer = data;
            this.answerService.isEdit = false;
            this.answerService.isShow = true;
        },(error)=>
        {
            console.log(error);
        },() =>
        {
            this.answerService.showData(this.answer[0]);
            this.router.navigate(['answer-detail'])
        }
        );
    }
}
