import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ServiceGeneral } from 'app/interfaces/services';
import { Rating } from 'app/models/ratings';
import { SharedService } from '../Shared/shared.service';

@Injectable({
  providedIn: 'root'
})
export class RatingService extends ServiceGeneral<Rating> {

  public api;
  public constructor(protected http:HttpClient,private shared:SharedService)
  {
    super(http);
    this.api = shared.api;
    this.url = `${this.api}/api/ratings`;
    this.headers = ['user','text','rating'];
  }
}
