import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { SharedService } from '../Shared/shared.service';


@Injectable({
  providedIn: 'root'
})
export class AgentReportService {

  public url;
  public agentsUrl;
  public agentLogsUrl;
  public api;
  public httpOptions:object = {}
  constructor(protected http:HttpClient,private shared:SharedService) {
    this.api = this.shared.api;
    this.url = `${this.api}/api/report/agent`;
    this.agentsUrl = `${this.api}/api/users/agents`;
    this.agentLogsUrl = `${this.api}/api/agent/log`; 
      this.httpOptions =  {
        headers: new HttpHeaders({
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Content-Type': 'application/json'
        })
      }
  }

  getData()
  {
    return this.http.get(this.url,this.httpOptions);
  }

  getAgents()
  {
    return this.http.get(this.agentsUrl,this.httpOptions);
  }
  getAgentData(id,start,end)
  {
    return this.http.get(this.url + '/' + id + '?start=' + start + '&' + 'end=' + end,this.httpOptions);
  }

  getAgentUserLog(session)
  {
    this.httpOptions['responseType'] = 'text';
    return this.http.get(`${this.agentLogsUrl}/${session}`,this.httpOptions);
  }
}
