import { Injectable } from '@angular/core';
import { Adapter } from 'app/adapter';
import { User } from './user';

export class Rating
{
    public constructor(
        public id:number,
        public user:string,
        public text:string,
        public rating:number
    ) { }
}


@Injectable({
    providedIn: "root",
})
export class RatingAdapter implements Adapter<Rating>
{
    adapt(item: any): Rating {
        return new Rating(item.id, item.user.name, item.text, item.rating);
      }
}