import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ServiceGeneral } from 'app/interfaces/services';
import { ServiceInquiry } from 'app/models/service_inquiry';
import { share } from 'rxjs/operators';
import { SharedService } from '../Shared/shared.service';

@Injectable({
  providedIn: 'root'
})
export class ServiceInquiryService extends ServiceGeneral<ServiceInquiry> {

  public api;
  public constructor(protected  http:HttpClient,private shared:SharedService)
  {
    super(http);
    this.api = shared.api;
    this.url = `${this.api}/api/service`;
    this.headers = ['name','email','mobile','answer'];
  }
}
