import { Injectable } from '@angular/core';
import { Adapter } from 'app/adapter';

export class Suggestion
{
    public constructor(
      public id : number,
      public message:string,
      public count:number,
      public answer:number
    ){}
}


@Injectable({
    providedIn: "root",
  })
  export class SuggestionAdapter implements Adapter<Suggestion> {
    adapt(item: any): Suggestion {
      return new Suggestion(item.id, item.message, item.count, item.answer_id);
    }
  }