import { Component, EventEmitter, OnInit } from '@angular/core';
import Chart from 'chart.js';
import {AnswerService} from '../../Services/answer/answer.service';
import {Answer, AnswerAdapter} from '../../models/answer';
import { KeysPipe } from 'app/pipes/keys.pipe';
import {map} from 'rxjs/operators';
import { Router } from '@angular/router';
import { SharedService } from 'app/Services/Shared/shared.service';



@Component({
    selector: 'answers',
    moduleId: module.id,
    templateUrl: 'answer.component.html',
    styleUrls: ['./answer.component.css'],
    providers:[KeysPipe]
})

export class AnswerComponent implements OnInit{
    public data:Answer[]
    public headers:Array<any>
    public total:number;
    public title = 'answers';
    public constructor(private answerSerivce:AnswerService,private  adapter:AnswerAdapter,private router:Router,private shared:SharedService)
    {
    }

    ngOnInit(){

        this.answerSerivce.index = 1;
        this.answerSerivce.getData(this.adapter).subscribe((data:Answer[])=>
        {
            if (data) {
                hideloader();
            }
            console.log(data)
            this.total = this.answerSerivce.total;
            this.data = data;
        })
        this.headers = this.answerSerivce.getHeaders();
        // Function is defined
        function hideloader() {
  
            // Setting display of spinner
            // element to none
            document.getElementById('loadContainer')
                .style.display = 'none';
        }
    }

    deleteData(dataItem:Answer) : void
    {

        this.answerSerivce.deleteData(dataItem.id).subscribe(data=>{
            this.data = this.data.filter((item) =>
            {
                if(item.id === dataItem.id)
                {
                    return false;
                }
                return true;  
            })
        })
    }



    updateData(data:Answer) : void
    {
        this.answerSerivce.showData(data);
        this.answerSerivce.isEdit = true;
        this.answerSerivce.isShow = false;
        this.answerSerivce.isAnswer = false;
        this.router.navigate(['answer-detail']);
    }


    addData() : void
    {
        this.answerSerivce.isEdit = false;
        this.answerSerivce.isShow = false;
        this.answerSerivce.isAnswer = true;
        this.router.navigate(['answer-detail']);
    }

    paginateData(event) : void
    {
        this.answerSerivce.index = event;
        this.answerSerivce.getData(this.adapter).subscribe((data:Answer[])=>
        {
            this.data = data;
        })
    }

    getRecordData(data : Answer) : void
    {
        this.answerSerivce.showData(data);
        this.answerSerivce.isEdit = false;
        this.answerSerivce.isShow = true;
        this.answerSerivce.isAnswer = false;
        this.router.navigate(['answer-detail']);
    }
}
