import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { SharedService } from '../Shared/shared.service';




@Injectable({
  providedIn: 'root'
})
export class AnswerReportService {

  public url;
  public httpOptions:object;
  public api;
  constructor(protected http:HttpClient,private shared:SharedService) {
    this.api = shared.api;
    this.url = `${this.api}/api/report`;

      this.httpOptions = {
        headers: new HttpHeaders({
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Content-Type': 'application/json'
        })
    }
}

  getData()
  {
    return this.http.get(this.url,this.httpOptions);
  }

  getAnswer(id,start,end)
  {
    return this.http.get(this.url + '/' + id + '?start=' + start + '&' + 'end=' + end,this.httpOptions);
  }

}
