import { INFERRED_TYPE } from '@angular/compiler/src/output/output_ast';
import { Component, Input, OnInit, Output ,EventEmitter} from '@angular/core';
import { Router } from '@angular/router';
import { KeysPipe } from 'app/pipes/keys.pipe';
import {Answer} from '../../models/answer';

@Component({
    selector: 'table-cmp',
    moduleId: module.id,
    templateUrl: 'table.component.html',
    providers:[KeysPipe]
})



export class TableComponent implements OnInit{
    @Input() isEdit:boolean = false;
    @Input() isView:boolean = false;
    @Input() isDelete:boolean = false;
    @Input() total:number;
    @Input() data:any[]
    @Input() isActive:number;
    @Input() headers:any[]
    @Output() deleteData:EventEmitter<any> = new EventEmitter();
    @Output() showData:EventEmitter<any> = new EventEmitter();
    @Output() updateData:EventEmitter<any> = new EventEmitter();
    @Output() paginateData:EventEmitter<any> = new EventEmitter();
    @Output() getRecordData:EventEmitter<any> = new EventEmitter();
    public start:number = 1;


    
    public  constructor(public router:Router)
    {
        
    }
    
    ngOnInit(){
    }

    ngDoCheck() 
    {
    }

    next()
    {

        if((this.start + 3) <= this.total)
        {
            this.start = this.start + 3;
            this.paginateData.emit(this.start);
        }
        else
        {
        }
    }

    paginate(event)
    {
        this.paginateData.emit(event.target.innerHTML);
    }

    delete(data)
    {
        this.deleteData.emit(data);
    }

    show(data)
    {
        this.showData.emit(data);
    }

    update(data)
    {
        this.updateData.emit(data);
    }

    getRecord(event)
    {
        this.getRecordData.emit(event);
    }

    previous()
    {
        this.start = this.start - 3;
        this.paginateData.emit(this.start);
    }

    progress(item)
    {
        item.isActive += 1;
        if(item.isActive == 3)
        {
            item.isActive = 0;
        }
    }
}
