import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Message, MessageAdapter } from 'app/models/message';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { SharedService } from '../Shared/shared.service';




@Injectable({
  providedIn: 'root'
})
export class MessageService {
  public messages:Array<any>
  public url:string;
  public messageUrl:string;
  public api;
  public httpOptions:object = {};
  constructor(private http:HttpClient,private adapter:MessageAdapter,private shared:SharedService) { 
  this.api = shared.api;
  this.url = `${this.api}/api/answers`;
  this.messageUrl = `${this.api}/api/message`;
    this.httpOptions = {
      headers: new HttpHeaders({
        'Authorization': `Bearer ${localStorage.getItem('token')}`,
        'Content-Type': 'application/json'
      })
    }
  }

  showData(id:number):Observable<Message[]>
  {
    return this.http.get<Message[]>(`${this.url}/${id}/messages`,this.httpOptions).pipe(map((data: any) => data.data.map((item) => this.adapter.adapt(item))));
  }

  deleteData(id:number):Observable<any>
  {
    return this.http.delete<any>(`${this.messageUrl}/${id}`,this.httpOptions);
  }

  addData(body:any)
  {
    return this.http.post<Message>(this.messageUrl,body,this.httpOptions).pipe(map((data: any) => data.data.map((item) => this.adapter.adapt(item))));
  }

  updateData(id,body:any)
  {
    return this.http.post<Message>(`${this.messageUrl}/${id}`,body,this.httpOptions);
  }
}
