import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ServiceGeneral } from 'app/interfaces/services';
import { Suggestion } from 'app/models/suggestion';
import { SharedService } from '../Shared/shared.service';

@Injectable({
  providedIn: 'root'
})
export class SuggestionService extends ServiceGeneral<Suggestion>{

  public api;
  public constructor(protected  http:HttpClient,private shared:SharedService)
  {
    super(http);
    this.api = shared.api;
    this.url = `${this.api}/api/suggestions`;
    this.headers = ['message','count','answer'];
  }
}
