import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AnswerAdapter } from 'app/models/answer';
import { AnswerReportService } from 'app/Services/answer-report/answer-report.service';
import { AnswerService } from 'app/Services/answer/answer.service';

@Component({
  selector: 'app-answer-report',
  templateUrl: './answer-report.component.html',
  styleUrls: ['./answer-report.component.css']
})
export class AnswerReportComponent implements OnInit {

  public data;
  public arrayData = [];
  public total;
  public current = 0;
  public answers;
  public start = 1;
  constructor(protected reportService:AnswerReportService,private answerService:AnswerService,private adapter:AnswerAdapter,private router:Router) { }

  ngOnInit(): void {

    this.reportService.getData().subscribe((data) => 
    {
      let i = 0;
      let j = 10;
      this.data = data;
      while(j <= this.data.length)
      {
        let array = this.data.slice(i,j);
        this.arrayData.push(array);
        i = j;
        j += 10;
        if(j > this.data.length)
        {
          array = this.data.slice(i,this.data.length);
          this.arrayData.push(array);
        }
      }
      
      this.total = this.arrayData.length - 1;
      this.data = this.arrayData[this.current];
    });

    this.answerService.showAll(this.adapter).subscribe((data) =>
    {
      if (data) {
        hideloader();
    }
        this.answers = data;
    })
    // Function is defined
    function hideloader() {
  
      // Setting display of spinner
      // element to none
      document.getElementById('loadContainer')
          .style.display = 'none';
  }
  }

  getReport()
  {
    let answer = (<HTMLInputElement>document.getElementById('answers')).value;
    if(answer == 'all')
    {
        this.data = this.arrayData[this.current];
        this.total = this.arrayData.length - 1;
    }
    else
    {
      this.data=[];
      document.getElementById('loadContainer')
          .style.display = 'block';
      let start = (<HTMLInputElement>document.getElementById('from')).value;
      let end = (<HTMLInputElement>document.getElementById('to')).value;
  
      this.reportService.getAnswer(answer,start,end).subscribe((data) =>
      {
        if (data) {
          hideloader();
      }
        this.total = 1;
        this.data = [data];
      })
    }
    // Function is defined
    function hideloader() {
  
      // Setting display of spinner
      // element to none
      document.getElementById('loadContainer')
          .style.display = 'none';
  }
  }


  paginate($event)
  {
    let target = $event.target.innerHTML;
    this.data = this.arrayData[target];
  }

  next()
  {

        if((this.start + 3) <= this.total)
        {
            this.start = this.start + 3;
            this.data = this.arrayData[this.start];
        }
  }

  previous()
  {
      this.start = this.start - 3;
      this.data = this.arrayData[this.start];
    }


}
