import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BuisnessInquiry, BuisnessInquiryAdapter } from 'app/models/buisness_inquiry';
import { BuisnessInquiryService } from 'app/Services/buisness_inquiry/buisness-inquiry.service';

@Component({
  selector: 'app-buisness-inquiry',
  templateUrl: './buisness-inquiry.component.html',
  styleUrls: ['./buisness-inquiry.component.css']
})
export class BuisnessInquiryComponent implements OnInit {

  public data:BuisnessInquiry[];
  public headers:Array<any>;
  public total:number;
  constructor(private inquiryService:BuisnessInquiryService,private adapter:BuisnessInquiryAdapter,private router:Router) { }

  ngOnInit(){

    this.inquiryService.getData(this.adapter).subscribe((data:BuisnessInquiry[])=>
    {
      if (data) {
        hideloader();
    }
        this.total = this.inquiryService.total;
        this.data = data;
    })
    this.headers = this.inquiryService.getHeaders();
    // Function is defined
    function hideloader() {
  
      // Setting display of spinner
      // element to none
      document.getElementById('loadContainer')
          .style.display = 'none';
  }
}

  paginateData(event) : void
  {
      this.inquiryService.index = event;
      this.inquiryService.getData(this.adapter).subscribe((data:BuisnessInquiry[])=>
      {
          this.data = data;
      })
  }

}
