import { Injectable } from '@angular/core';
import { Adapter } from 'app/adapter';

export class ServiceInquiry
{
    public constructor(
        public id:number,
        public name:string,
        public email:string,
        public mobile:string,
        public answer:string,
        public isActive:boolean,
        public answer_id?:number
    ){};
}

@Injectable({
    providedIn: "root",
})
export class ServiceInquiryAdapter implements Adapter<ServiceInquiry>
{
    adapt(item: any): ServiceInquiry {
        return new ServiceInquiry(item.id, item.user.name,item.user.email,item.user.msisdn, item.answer,item.isActive);
      }
}
