import { Injectable } from '@angular/core';
import { Adapter } from 'app/adapter';

export class BuisnessInquiry
{
    constructor(
        public id:number,
        public name:string,
        public email:string,
        public mobile:string,
        public type:string,
        public related:string,
        public isActive:boolean
    ){}
}

@Injectable({
    providedIn: "root",
})
export class BuisnessInquiryAdapter implements Adapter<BuisnessInquiry>
{
    adapt(item: any): BuisnessInquiry {
        return new BuisnessInquiry(item.id,item.user.name,item.user.email, item.user.msisdn, item.type,item.related,item.isActive);
      }
}