import { Component, OnInit } from '@angular/core';
import { Answer, AnswerAdapter } from 'app/models/answer';
import { AnswerService } from 'app/Services/answer/answer.service';
import { Router } from '@angular/router';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith,filter} from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { MessageService } from 'app/Services/message/message.service';
import { Message } from 'app/models/message';


@Component({
  selector: 'app-answer-detail',
  templateUrl: './answer-detail.component.html',
  styleUrls: ['./answer-detail.component.css']
})
export class AnswerDetailComponent implements OnInit {
  public title:string
  public data:Answer;
  public isEdit:boolean ;
  public isShow:boolean;
  public isAnswer:boolean;
  public messages:Array<any>;
  public options:Answer[];
  public choice:string;
  public parent:string = null;
  public add_message:string = '';
  myControl = new FormControl();
  public filteredOptions: Observable<Answer[]>;


  constructor(private answerService:AnswerService,private router:Router,private adapter:AnswerAdapter,private http:HttpClient,private messageService:MessageService) {
    
    this.title = 'answer_detail';
    this.isEdit = this.answerService.isEdit;
    this.isShow = this.answerService.isShow;
    this.isAnswer = this.answerService.isAnswer;

    if(this.isAnswer == true)
    {
      this.data = new Answer(null,null,null,null,null);
    }
    else
    {
      this.showData();
      this.showMessage();
    }
   }
  
   ngDoCheck()
  {
 

  }

  ngOnInit(): void {
    
      this.answerService.showAll(this.adapter).subscribe((data:Answer[])=>
      {
        this.options = data;
        this.filteredOptions = this.myControl.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.id),
          map(name => name ? this._filter(name) : this.options.slice())
        );
      })
    }

  showData() : void
  {
    if(this.answerService.sharedMessage != undefined)
    {
      this.answerService.sharedMessage.subscribe((data =>{
        this.data = data;
        // this.answerService.showItem(this.adapter,this.data.parent).subscribe((data:any) =>
        // {
        //   this.parent = data[0].answer;
          
        // })
      }));
    }
    else
    {
      this.router.navigate(['/answers']);
    }

  }

  addRow() : void
  {
    let body = {
      'message' : this.add_message,
      'answer_id' : this.data.id
    }
    this.messageService.addData(body).subscribe( (data:Message) =>
    {
      this.messages.push({'id' : data[0].id,'message':data[0].message})
    });
  }

  saveData(data:Answer) 
  {
      this.answerService.saveData(data).subscribe((data => 
      this.router.navigate(['answers'])));
  }

  displayFn(answer: Answer): string {
    return answer && answer.text ? answer.text : '';
  }

  private _filter(answer: string): Answer[] {
    const filterValue = answer;
    console.log(this.options);
    return this.options.filter(option =>  option.text.indexOf(filterValue) === 0);
  }

  showMessage()
  {
    this.messageService.showData(this.data.id).subscribe((data:Message[]) =>
    {
      this.messages = data;
    });
  }

  deleteMessage(item:Message)
  {
    this.messageService.deleteData(item.id).subscribe((data) => 
    {
      this.messages = this.messages.filter((data:Message) => 
      {
          if(data.id == item.id)
          {
            return false;
          }
          return true;
      })
    })
  }

  updateMessage(event,item:Message)
  {
    document.getElementById('message_' + item.id).removeAttribute('readonly');
  }

  updateData()
  {
    if(this.myControl.value != null)
    {
      // this.data.parent = this.myControl.value.id;
    }

    this.messages.map((data) =>
    {
        let value = (<HTMLInputElement>document.getElementById('message_' + data.id)).value;
        data.message = value;
    });

    let body = {
      'id' : this.data.id,
      'answer' : this.data,
      'message' : this.messages
    };
    

    this.answerService.updateData(body).subscribe((data) => 
    {
      this.router.navigate(['answers']);
    })
  }

  edit(event,item)
  {
    if(event.keyCode == 13)
    { 
      if(item.message != event.target.value)
      {
        this.messageService.updateData(item.id, { 'message' : event.target.value}).subscribe((data) =>
        {
        });
      }
      event.target.setAttribute('readonly','');
    }
  }

}
