import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Rating, RatingAdapter } from 'app/models/ratings';
import { RatingService } from 'app/Services/rating/rating.service';

@Component({
  selector: 'app-rating',
  templateUrl: './rating.component.html',
  styleUrls: ['./rating.component.css']
})
export class RatingComponent implements OnInit {

  public data:Rating[];
  public headers:Array<any>;
  public total:number;
  constructor(private rateService:RatingService,private adapter:RatingAdapter,private router:Router) { }

  ngOnInit(){

    this.rateService.getData(this.adapter).subscribe((data:Rating[])=>
    {
      if (data) {
        hideloader();
    }
      this.total = this.rateService.total;
        this.data = data;
    })
    this.headers = this.rateService.getHeaders();
    // Function is defined
    function hideloader() {
  
      // Setting display of spinner
      // element to none
      document.getElementById('loadContainer')
          .style.display = 'none';
  }
}

  paginateData(event) : void
  {
      this.rateService.index = event;
      this.rateService.getData(this.adapter).subscribe((data:Rating[])=>
      {
          this.data = data;
      })
  }

}
